html{
	height: 100%;
	overflow: hidden;
}
body{
	padding: 0;
	margin:0;
	background: url(../img/bg.jpg) no-repeat;
	background-size:cover;
	background-attachment:fixed;
	background-color:#9fafca;
	height:100%;
	font-family:'Gotham';
	-webkit-font-smoothing: antialiased;
}
*::-webkit-scrollbar {
  width: 14px;
  position: fixed;
  right: 0
 }

*::-webkit-scrollbar-track,
::-webkit-scrollbar-thumb {
  border: 4px solid rgba(255, 255, 255, 0);
  background-clip: padding-box; }

*::-webkit-scrollbar-track {
  background-color: rgba(255, 255, 255, 0); }
 
*::-webkit-scrollbar-thumb {
  background-color: #d8d8d84d;
  border-radius: 14px;
  opacity: 0.77; }
img{
	width: 100%;
	height: auto;
	display: block;
}
ul{
	list-style: none;
	padding: 0;
	margin:0;
	text-align:left;
}
img.tall{
	width: auto;
	height: 100%;

}
header.flipped {
	height: 6vw;
	.flipper{
		transform:rotateX(180deg);
	}
}
header{
	z-index: 1000;
	height: 10.8vw;
	position: fixed;
	width: 100%;
	transform:rotateX(0deg);
	transition: height 0.6s;
	.flipper{
		transform-style: preserve-3d;
		transition: 0.6s;
		height: 100%;
		
		.front,.back{
			backface-visibility: hidden;
			box-shadow:0px 11px 28px 11px #00000054;
			position: absolute;
			height: 100%;
			left: 0;
			width: 100%;
			
		}
	}
	.front{
		transform:rotateX(0deg);
		z-index: 2;
		background: #fff;
		
		top: 0;
		.logo{
			background: url(../img/dwa_logo.png) no-repeat;
			background-size:100% auto;
			width: 0px;
			height: 79.8%;
		    padding-left: 8.6%;
		    top: 8%;
		    position: absolute;
		    left: 30.54%;
		    a{
		    	display: block;
		    }
		}
		.text{
			color: #092e6e;
			position: absolute;
			&.fyc{
				font-weight:bold;
				left: 40.5%;
				top: 56.1%;
				font-size:1.97vw;

			}
			&.awards{
				left: 40.5%;
				top: 25%;
				font-size:4vw;
				font-weight:300;
			}
		}
	}
	.back{
		transform:rotateX(180deg);
		background: #fff;
		
		bottom: 0;
		.logo{
			background: url(../img/dwa_horizontal.png) no-repeat;
			background-size:100% auto;
			width: 0px;
			height: 100%;
		    padding-left: 22vw;
		    top: 0%;
		   	position: relative;
		    background-position:50%;
		    margin-left: 7.54vw;
		    display: inline-block;
		    a{
		    	display: block;
		    	position: absolute;
		    	width: 100%;
		    	height: 100%;
		    	left: 0;
		    }
		}
		.film-menu{
			display: inline-block;
			margin-left:6vw;
			line-height: 6vw;
			vertical-align: top;
		    font-weight: 800;
		    color: @blue;
		    width: 25vw;
		    font-size: 1.3vw;
		    letter-spacing: 4px;
		    margin-right:4vw;
			
			> div {
				display: inline-block;
				margin-right:3.1vw;
				cursor: pointer;
				&:last-child{
					margin: 0;
				}
				&.active{
					color: @yellow;
				}
			}
		}
		.text{
			color: #b3b3b3;
			position: relative;
			display:inline-block;
			line-height:6vw;
			vertical-align: top;

			&.fyc{
				font-weight:800;
				font-size:1.4vw;

			}
			&.awards{
				font-size:1.3vw;
				font-weight:400;
				margin-right:0.3vw;
				letter-spacing: 0.1vw;
			}
		}
	}
}
.content{
	position: relative;
	
	height:100%;
	text-align:center;
	transition: height 0.6s;
	&.flipped{
		
	}
	.page{
		width: 100%;
		min-height: ~'calc(100% - 6vw)';
		position: absolute;
		top: 0;
		padding-top:6vw;
		display: none;
		
		.static{
			position: absolute;
		    width: 30vw;
		    margin-left: 6vw;
		    margin-top: 29px;
		    z-index: 1000;
		    display: none;
		    .logo{
			    height: 0; 
			}
		}
		.subpage{
		    position: absolute;
    		width: 100%;
    		top: 6vw;
    		z-index: 1;
    		transform:translateY(100%);
    		transition: transform 0.6s;
    		overflow-y:auto;
    		&.on,&.alwaysOn{
    			transform:translateY(0%);
    		}
    		&.landing{

    		}
    		&.photos{
    			overflow: hidden;
				max-height:~'calc(100% - 6vw)';
    			.slider{
    				height: 100%;
    				text-align:left;
    				position: absolute;
    				top: 0;
    				background:#000;
    				img{
    					float: left;
    				}
    				.slide {
    					float: left;
    					
					    height: 100%;
					    background-size: cover;
					    background-repeat: no-repeat;
					    background-position: 50%;
    				}

    			}
    			.arrow{
    				width: 20px;
    				height: 20px;
    				position: absolute;
    				bottom: 36px;
    				background-size:cover;
    				cursor: pointer;
    				&.disabled{
    					display: none;
    				}
    				&.left{
    					background-image:url(../img/left-arrow.png);
    					right: 92px;
    				}
    				&.right{
    					background-image:url(../img/right-arrow.png);
    					right:38px;
    				}
    			}
    			.number{
    				background:@blue;
				    height: 30px;
				    width: 20px;
				    line-height: 30px;
				    color: #fff;
				    position: absolute;
				    right: 65px;
				    bottom: 30px;
				    font-weight:bold;
    			}
    		}
    		&.videos{
    			z-index: 1001;
    			top: 0;
    			height: 100%;
    			width: 100%;
    			background:#000;
    			video{
    				height: auto;
    				width: 100%;
    				display: block;
    				position: absolute;
    				top: 50%;
    				transform:translateY(-50%);
    			}
    			.close-btn{
    				position: absolute;
    				right: 2vw;
    				top: 2vw;
    				font-size:5vw;
    				color: #fff;
				    transform: rotate(45deg);
				    cursor: pointer;
				    &:hover{
				    	color: @yellow;
				    }
    			}
    		}
		}
		.left.col{
			position: relative;
		    padding-left: 7%;
			.menu{
				
				.nav{
					color:@blue;
					font-weight:800;
					font-size:1vw;
					letter-spacing: 0.1vw;
					> li.current{
						padding-left:5%;
						&:before{
							content:"";
							background: @blue;
						    width: 2%;
						    height: 1em;
						    top: 0.2em;
						    left: 0;
						    display: block;
						   	position: absolute;
						}
					}
					li{
						cursor: pointer;
						line-height:1.4em;
						position: relative;
						margin-bottom:3px;
						&.current{
							color:@yellow;
							
							
							.sub-nav{
								display: block;
								color: @blue;
								font-size:0.9em;
								> li.current{
									color:@yellow;
									
								}
								> li {
								    
								}
							}
						}
					}
					.sub-nav{
						display: none;
					}
				}
			}
		}
		.col.right{
			width: 67%;
		    position: absolute;
		    top: 19%;
		    right: 5vw;
			> div{
				display: none;
				position: absolute;
				&:first-child{
					display: block;
				}
			
		
				
				.maker-photo{
					width:30%;
					display: inline-block;

				}
				.bio{
					font-size:1.1vw;
					h4{
						margin-top:1%;
					}
					display: inline-block;
					width: 66%;
					text-align:left;
					color: #1a3464;
				    margin-left: 3%;
				    padding-bottom:10%;
				}
			}
		}
	}
	.home{
		overflow: hidden;
		min-height: ~'calc(100% - 10.8vw)';
		display: block;
		padding-top:10.8vw;
		h3{
			color:#fff;
			font-weight:400;
			font-size:2.4vw;
			margin:2% 0 0;
			text-shadow: 1px 1px 1px #00000075;
		}
		.poster{
			height: 45vw;
			margin-top:2%;
			display: inline-block;
			box-shadow:8px 12px 24px 0px #0000007d;
			transition: transform 1s;
			cursor: pointer;

			&#bird-karma{
				margin-right: 7%;
			}
			&:hover{
				transform:scale(1.08);
			}
		}
	}
